import React from 'react';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { StyledListItem } from '../BenefCardList/SearchList/SearchList.style';

const AccountCardItem = props => (
  <StyledListItem
    key={props.index}
    onClick={props.onclick}
    className={`row compte mx-auto`}
    active={props.active}
  >
    <div className="col text-left">
      <strong className="ttr-account">{props.accountName}</strong>
      <br />
      <span className="libelle-account">
        <i className="icon icon-man"></i> N° {props.accountNumber}
      </span>
    </div>
    <div className="col text-right">
      {t('Components:balance')}
      <span className="account-montant">
        {' '}
        : {props.accountBalance} {t('Components:currency')}
      </span>
      <br />
    </div>
  </StyledListItem>
);

export default withTranslation()(AccountCardItem);
