import React from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import FormatDate from '../../../components/FormatDateComponenth/FormatDateComponent';
import DateCalendar from '../../../components/DateCalendar/DateCalendar';
import Panel from '../../../components/Panel/Panel';
import HistoryTable from '../../../components/DataTable/CustomDataTable.jsx';
import TransfersHistoryTemplate from 'pdf/Templates/TransfersHistoryTemplate';
import TransfersHistoryTemplateExcel from 'excel/Templates/TransfersHistoryTemplateExcel';
import { getHistoryCashTransfer } from '../../../redux/cashTransfer/cashTransfer.actions';
import { HISTORY_NAMESPACE } from '../constants';
import { MultiSelect } from 'primereact/multiselect';
import { dateFilter } from '../../../utils/DateUtils';
import { removeRepeatedElements } from '../../../utils/TableUtils';
import { exportTableToPdf, exportTableToExcel } from 'utils/exportUtils';
import { getSelectedAccountNumber } from 'redux/accounts/accounts.selectors';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import AccountColumn from '../../../components/AccountColumn/AccountColumn';

const BeneficiaryColumn = props => (
  <span className="text-strong">
    {props.labelBeneficiary}
    <br />
    <span className="text-light">ID : {props.beneficiaryHistoricId}</span>
  </span>
);
const DateColumn = props => <FormatDate date={props.date} value={true} />;
let accounts = [];
class CashTransferHistory extends React.Component {
  constructor() {
    super();
    let maxDate = new Date();
    this.state = {
      loading: true,
      totalRecords: 0,
      maxDate: maxDate,
      accountUnique: null,
    };
  }
  _dateChangeHandler = () => {
    this.forceUpdate();
  };

  componentDidMount() {
    this.props.getHistory();
  }
  _buildDataTableRows = () =>
    this.props.history.map(data => {
      const {
        addTime,
        clientAccount,
        beneficiary,
        executionReference,
        amount,
      } = data;
      accounts.push(clientAccount.accountName);
      return {
        date: addTime,
        clientNum: clientAccount.accountNumber,
        clientName: clientAccount.accountName,
        beneficiaryName: beneficiary.name,
        beneficiaryNumber: beneficiary.beneficiaryId,
        code: executionReference,
        amount: amount,
      };
    });

  _clientTemplate = (rowData, column) => (
    <AccountColumn
      accountName={rowData.clientName}
      accountNumber={rowData.clientNum}
    />
  );
  _beneficiaryTemplate = (rowData, column) => (
    <BeneficiaryColumn
      beneficiaryname={rowData.beneficiaryName}
      beneficiaryNumber={rowData.beneficiaryNumber}
    />
  );
  _dateTemplate = (rowData, column) => <DateColumn date={rowData.date} />;
  _onAccountUniqueChange = event => {
    this.dt.filter(event.value, 'clientName', 'in');
    this.setState({ accountUnique: event.value });
  };
  _accountFilterComponent = (element, options) => (
    <MultiSelect
      filter={true}
      style={{ width: '100%' }}
      value={element}
      options={options}
      onChange={this._onAccountUniqueChange}
    />
  );
  _dateCalendarComponent = maxDate => (
    <DateCalendar
      maxDate={maxDate}
      handleDateChange={e => {
        this.dt.filter(e.value, 'date', 'custom');
        this._dateChangeHandler();
      }}
    />
  );
  _balanceFormatter = data => (
    <FormattedNumber
      number={data.amount}
      format={FormatsConfiguration.AmountFormat}
      currency={t('Components:currency')}
    />
  );
  _getPdfFileName = () =>
    `${t(`${HISTORY_NAMESPACE}history`)}_${
      this.props.selectedAccountNumber
    }.pdf`;
  _getSheetName = () =>
    `${t(`${HISTORY_NAMESPACE}history`)}_${this.props.selectedAccountNumber}`;
  render() {
    let rows = this._buildDataTableRows();
    let uniqueAccount = removeRepeatedElements(accounts);
    let accountsUnique = uniqueAccount.map(account => ({
      label: account,
      value: account,
    }));
    const pdfIdLogo = 'pdfCashTransfersLogo';
    const pdfIdHeader = 'pdfCashTransfersHeader';
    const pdfIdTable = 'pdfCashTransfersTable';
    const excelId = 'excelCashTransfers';
    return (
      <div className="row mx-auto">
        <div className="col-sm-12">
          <TransfersHistoryTemplate
            title={t(`${HISTORY_NAMESPACE}cashTransferHistoryPanelTitle`)}
            idLogo="pdfCashTransfersLogo"
            idHeader="pdfCashTransfersHeader"
            idTable="pdfCashTransfersTable"
            tableRows={rows}
          />
          <TransfersHistoryTemplateExcel
            title={t(`${HISTORY_NAMESPACE}cashTransferHistoryPanelTitle`)}
            excelId={excelId}
            tableRows={rows}
          />
          <Panel
            title={t(`${HISTORY_NAMESPACE}cashTransferHistoryPanelTitle`)}
            hasDownloadLink
            pdfLinkAction={() =>
              exportTableToPdf(
                pdfIdHeader,
                pdfIdLogo,
                pdfIdTable,
                this._getPdfFileName(),
              )
            }
            csvLinkAction={() =>
              exportTableToExcel(excelId, this._getSheetName())
            }
          >
            <HistoryTable
              value={rows}
              onRowReorder={e => (rows = e.value)}
              reference={el => (this.dt = el)}
              columnResizeMode="fit"
              cols={[
                {
                  field: 'date',
                  header: t(`${HISTORY_NAMESPACE}dateClnLabel`),
                  body: this._dateTemplate,
                  filterElement: this._dateCalendarComponent(
                    this.state.maxDate,
                  ),
                  filterFunction: dateFilter,
                  filter: true,
                },
                {
                  field: 'clientName',
                  header: t(`${HISTORY_NAMESPACE}accountClnLabel`),
                  body: this._clientTemplate,
                  filterElement: this._accountFilterComponent(
                    this.state.accountUnique,
                    accountsUnique,
                  ),
                  filter: true,
                },
                {
                  field: 'beneficiaryName',
                  header: t(`${HISTORY_NAMESPACE}beneficiaryClnLabel`),
                  body: this._beneficiaryTemplate,
                },
                {
                  field: 'code',
                  header: t(`${HISTORY_NAMESPACE}referenceClnLabel`),
                },
                {
                  field: 'amount',
                  header: t(`${HISTORY_NAMESPACE}amountClnLabel`),
                  body: this._balanceFormatter,
                },
              ]}
            />
          </Panel>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getHistory: () => {
    dispatch(getHistoryCashTransfer());
  },
});

const mapStateToProps = state => ({
  history: state.CashTransfer.history,
  selectedAccountNumber: getSelectedAccountNumber(state),
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(CashTransferHistory),
);
