import { actionCreator } from '../../utils/generators';
import types from './favorites.types';

export const getFavorites = () => ({
  type: actionCreator('req', types.GET_FAVORITES),
});
export const deleteFavorite = (transactionType, transactionId) => ({
  type: actionCreator('req', types.DELETE_FAVORITE),
  payload: { transactionType, transactionId },
});
