import React from 'react';
import { connect } from 'react-redux';
import Stepper from '../../../components/Stepper/Stepper';
import BeneficiaryStepIcon from '../../../assets/SvgComponents/StepperIcons/BeneficiaryStepIcon';
import AmountStepIcon from '../../../assets/SvgComponents/StepperIcons/AmountStepIcon';
import RecapStepIcon from '../../../assets/SvgComponents/StepperIcons/RecapStepIcon';
import ConfirmationStepIcon from '../../../assets/SvgComponents/StepperIcons/ConfirmationStepIcon';
import BeneficiaryStep from '../../../components/Beneficiaries/BeneficiariesStep';
import MontantStep from '../../../components/MontantForm/MontantFormStep';
import RecapStep from '../../../components/Recap/RecapStep';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { getAccounts } from '../../../redux/accounts/accounts.actions';
import { TRANSFER_NAMESPACE } from '../constants';
import { getTransferBeneficiaries } from '../../../redux/beneficiaries/beneficiaries.actions';
import {
  initTransfer,
  validateTransfer,
  resendOtpTransfer,
  passwordValidateTransfer,
  setToDefaultState,
  initCToCTransfer,
} from '../../../redux/transfer/transfer.actions';
import HandleBeneficiaryModal from './HandleBeneficiaryModal';
import { HandleBeneficiaryMode } from './BeneficiaryHandlerHelpers';
import ValidationRecap from '../../../components/Recap/RecapForm/ValidationRecap';
import { showMessage } from '../../../redux/messages/messages.actions';
const user = sessionStorage
  .getItem('SessionUserIndex')
  .concat(sessionStorage.getItem('SessionUserPhone'));
class Transfer extends React.Component {
  addBeneficiaryFormRef = null;
  state = {
    selectedAccountId: '',
    selectedBeneficiaryId: '',
    selectedAccount: {},
    selectedBeneficiary: {},
    destinationAccount: {},
    label: '',
    amount: '',
    favoris: false,
    codeValue: '',
    showAddBeneficiaryModal: false,
    accountSelected: false,
  };
  componentWillUnmount() {
    this.props.setToDefaultState();
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (prevProps.virData !== this.props.virData && this.props.virData) {
      this.props.showMessage(
        t(`${TRANSFER_NAMESPACE}transferIsDone`),
        'CONFIRM',
        t('Components:validate'),
        false,
      );
    }
  }
  _toggleAddBeneficiaryModal = () => {
    this.setState({
      showAddBeneficiaryModal: !this.state.showAddBeneficiaryModal,
    });
  };
  _submitAddBeneficiary = () => {
    this.addBeneficiaryFormRef.submitForm();
  };
  _setAddBeneficiaryFormRef = ref => {
    this.addBeneficiaryFormRef = ref;
  };
  _accountChangeHandler = accountId => {
    this.setState({ selectedAccountId: accountId, accountSelected: true });
  };
  _beneficiaryChangeHandler = beneficiaryId => {
    this.setState({ selectedBeneficiaryId: beneficiaryId });
  };
  _amountChangeHandler = amount => {
    this.setState({ amount: amount });
  };
  _labelChangeHandler = label => {
    this.setState({ label: label });
  };

  componentDidMount() {
    this.props.getAccounts();
    this.props.getTransferBeneficiaries();
  }
  _favorisHandler = childData => {
    this.setState({ favoris: childData });
  };
  handleChange = event => {
    this.setState({ codeValue: event.target.value });
  };
  submit1Handler = () => {
    let selectedClientAccount = this.props.accounts.filter(
      account => account.clientAccountId === this.state.selectedAccountId,
    );
    let selectedBeneficiary = this.props.transferBeneficiaries.filter(
      beneficiary =>
        beneficiary.beneficiaryId === this.state.selectedBeneficiaryId,
    );
    let destinationAccount = this.props.accounts.filter(
      account => account.clientAccountId === this.state.selectedBeneficiaryId,
    );
    this.setState(
      {
        selectedAccount: selectedClientAccount[0],
        selectedBeneficiary: selectedBeneficiary[0],
        destinationAccount: destinationAccount[0],
      },
      () => {
        if (this.state.selectedBeneficiary) {
          this.props.init(
            this.state.selectedAccount,
            this.state.selectedBeneficiary,
            this.state.label,
            this.state.amount,
            this.state.label,
            this.state.favoris,
          );
        }
        if (this.state.destinationAccount) {
          this.props.initCToC(
            this.state.amount,
            this.state.destinationAccount,
            this.state.selectedAccount,
            this.state.label,
            this.state.label,
          );
        }
      },
    );
  };
  submit2Handler = () => {
    switch (this.props.typeValidation) {
      case 'OTP':
        this.props.validate(
          this.props.otpId,
          this.state.codeValue,
          this.state.favoris,
          this.props.typeValidation,
        );
        break;
      case 'PASSWORD':
        this.props.passwordValidate(
          this.state.codeValue,
          this.state.favoris,
          user,
          this.props.typeValidation,
          this.props.virementId,
        );
        break;
      default:
        break;
    }
  };

  submit3Handler = () => {
    this.props.resendOtp(this.props.otpId);
  };
  render() {
    let fromFavorites = false;
    let favorite = null;
    if (this.props.activeTabParams !== undefined) {
      fromFavorites = this.props.activeTabParams.fromFavorites;
      favorite = this.props.favorites.filter(
        transaction =>
          transaction.transactionType === 'transfer' &&
          transaction.transactionId ===
            this.props.activeTabParams.transactionId,
      )[0];
    }
    const accountsToBeneficiaries = () =>
      this.props.accounts
        .filter(
          account => account.clientAccountId !== this.state.selectedAccountId,
        )
        .map(account => ({
          beneficiaryId: account.clientAccountId,
          name: t(`${TRANSFER_NAMESPACE}mySelf`),
          type: 'physique',
          firstName: t(`${TRANSFER_NAMESPACE}me`),
          lastName: t(`${TRANSFER_NAMESPACE}even`),
          mobileNumber: '',
          bankCode: '',
          atmCode: '',
          accountNumber: account.accountNumber,
          ribKey: '',
          icon: '',
          scope: 'beneficiary_intern',
        }));
    return (
      <div className="row">
        <div className="col-sm-12">
          <div className="wizard-row">
            <div className="panel panel-default panel-wizard">
              <Stepper
                name={
                  this.state.selectedBeneficiary
                    ? this.state.selectedBeneficiary.name
                    : t(`${TRANSFER_NAMESPACE}mySelf`)
                }
                accountNumber={this.state.selectedAccount.accountNumber}
                amount={this.state.amount}
                goToNext={this.props.goToNext}
                fromFavorites={fromFavorites}
                favorite={favorite}
                startingStep={fromFavorites ? 3 : 1}
                elements={[
                  {
                    stepNumber: 0,
                    stepTitle: t(`${TRANSFER_NAMESPACE}beneficiaryStepTitle`),
                    stepIcon: <BeneficiaryStepIcon />,
                    stepRender: (
                      <BeneficiaryStep
                        handleBeneficiary={this._toggleAddBeneficiaryModal}
                        selectedAccount={
                          fromFavorites && favorite
                            ? favorite.clientAccount.clientAccountId
                            : this.state.selectedAccountId
                        }
                        selectedBeneficiary={
                          fromFavorites && favorite
                            ? favorite.beneficiary.beneficiaryId
                            : this.state.selectedBeneficiaryId
                        }
                        accountsList={this.props.accounts}
                        onAccountChange={this._accountChangeHandler}
                        showAlertEnabled={
                          this.props.transferBeneficiaries.length === 0
                        }
                        onBeneficiaryChange={this._beneficiaryChangeHandler}
                        beneficiariesList={
                          this.state.accountSelected
                            ? this.props.transferBeneficiaries.concat(
                                accountsToBeneficiaries(),
                              )
                            : this.props.transferBeneficiaries
                        }
                      />
                    ),
                  },
                  {
                    stepNumber: 1,
                    stepTitle: t(`${TRANSFER_NAMESPACE}amountStepTitle`),
                    stepIcon: <AmountStepIcon />,
                    stepRender: (
                      <MontantStep
                        amount={
                          fromFavorites && favorite
                            ? favorite.amount
                            : this._amountChangeHandler
                        }
                        label={
                          fromFavorites && favorite
                            ? favorite.description
                            : this._labelChangeHandler
                        }
                        isFavorite={
                          fromFavorites && favorite
                            ? true
                            : this._favorisHandler
                        }
                        favoriteLabel={
                          fromFavorites && favorite ? favorite.label : ''
                        }
                      />
                    ),
                  },
                  {
                    stepNumber: 2,
                    stepTitle: t(`${TRANSFER_NAMESPACE}recapStepTitle`),
                    stepIcon: <RecapStepIcon />,
                    stepRender: (
                      <RecapStep
                        title={t('Components:youHaveAskedForATransfert')}
                        name={
                          fromFavorites && favorite
                            ? favorite.beneficiary.name
                            : this.state.selectedBeneficiary
                            ? this.state.selectedBeneficiary.name
                            : t(`${TRANSFER_NAMESPACE}mySelf`)
                        }
                        number={
                          fromFavorites && favorite
                            ? favorite.beneficiary.mobileNumber
                            : this.state.selectedBeneficiary
                            ? this.state.selectedBeneficiary.mobileNumber
                            : t(`${TRANSFER_NAMESPACE}myMobileNumber`)
                        }
                        amount={
                          fromFavorites && favorite
                            ? favorite.amount
                            : this.state.amount
                        }
                        ownNumber={user}
                        onclickbutton={this.submit3Handler}
                        onCodeChange={this.handleChange}
                        codeValue={this.state.codeValue}
                      />
                    ),
                  },
                  {
                    stepNumber: 3,
                    stepTitle: t(`${TRANSFER_NAMESPACE}confirmationStepTitle`),
                    stepIcon: <ConfirmationStepIcon />,
                    stepRender: (
                      <ValidationRecap
                        title={t('Components:youHaveMadeATransferTo')}
                        name={
                          fromFavorites && favorite
                            ? favorite.beneficiary.name
                            : this.state.selectedBeneficiary
                            ? this.state.selectedBeneficiary.name
                            : t(`${TRANSFER_NAMESPACE}mySelf`)
                        }
                        number={
                          fromFavorites && favorite
                            ? favorite.beneficiary.mobileNumber
                            : this.state.selectedBeneficiary
                            ? this.state.selectedBeneficiary.mobileNumber
                            : t(`${TRANSFER_NAMESPACE}myMobileNumber`)
                        }
                        amount={
                          fromFavorites && favorite
                            ? favorite.amount
                            : this.state.amount
                        }
                        ownNumber={user}
                        onclickbutton={this.submit3Handler}
                        onCodeChange={this.handleChange}
                        codeValue={this.state.codeValue}
                      />
                    ),
                  },
                ]}
                dis1={
                  !fromFavorites &&
                  (this.state.selectedAccountId === '' ||
                    this.state.selectedBeneficiaryId === '')
                }
                dis2={
                  fromFavorites
                    ? this.state.pinValue === ''
                    : this.state.amount === '' || this.state.label === ''
                }
                dis3={this.state.codeValue === ''}
                resetStats={() => {
                  this.setState({
                    selectedAccount: {},
                    selectedBeneficiary: {},
                    selectedAccountId: '',
                    selectedBeneficiaryId: '',
                    amount: '',
                    label: '',
                    codeValue: '',
                  });
                }}
                action2={this.submit1Handler}
                ValidateAction={this.submit2Handler}
                ModalBackComp={
                  <BeneficiaryStep
                    accountsList={this.props.accounts}
                    onAccountChange={this._accountChangeHandler}
                    onBeneficiaryChange={this._beneficiaryChangeHandler}
                    beneficiariesList={this.props.transferBeneficiaries}
                  />
                }
                success={this.props.error === null}
                isLoading={this.props.isLoading}
                newprops={this.state.newstatevar}
              />
              {this.state.showAddBeneficiaryModal ? (
                <HandleBeneficiaryModal
                  show={this.state.showAddBeneficiaryModal}
                  handleClose={this._toggleAddBeneficiaryModal}
                  handleValidate={this._submitAddBeneficiary}
                  beneficiaryFormRefSetter={this._setAddBeneficiaryFormRef}
                  mode={HandleBeneficiaryMode.ADD_TRANSFER}
                />
              ) : null}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getTransferBeneficiaries: () => {
    dispatch(getTransferBeneficiaries());
  },
  showMessage: (text, severity, validateTitle, hasCancelBtn) => {
    dispatch(showMessage(text, severity, validateTitle, hasCancelBtn));
  },
  setToDefaultState: () => {
    dispatch(setToDefaultState());
  },
  getAccounts: () => {
    dispatch(getAccounts());
  },
  init: (clientAccount, beneficiary, label, amount, description, favoris) => {
    dispatch(
      initTransfer({
        clientAccount,
        beneficiary,
        label,
        amount,
        description,
        favoris,
      }),
    );
  },
  initCToC: (
    amount,
    clientAccountDst,
    clientAccountSrc,
    description,
    label,
  ) => {
    dispatch(
      initCToCTransfer({
        amount,
        clientAccountDst,
        clientAccountSrc,
        description,
        label,
      }),
    );
  },
  validate: (otpId, otpValue, favoris, typeValidation) => {
    dispatch(
      validateTransfer({
        otpId,
        otpValue,
        favoris,
        typeValidation,
      }),
    );
  },
  passwordValidate: (cricket, favoris, smiley, typeValidation, virementId) => {
    dispatch(
      passwordValidateTransfer({
        cricket,
        favoris,
        smiley,
        typeValidation,
        virementId,
      }),
    );
  },
  resendOtp: otpId => {
    dispatch(
      resendOtpTransfer({
        otpId,
      }),
    );
  },
});

const mapStateToProps = state => ({
  transferBeneficiaries: state.Beneficiaries.transferBeneficiaries,
  accounts: state.Accounts.accounts,
  isLoading: state.Transfer.isLoading,
  error: state.Transfer.error,
  activeTabParams: state.Menu.activeTabParams,
  favorites: state.Favorites.favorites,
  otpId: state.Transfer.otpId,
  typeValidation: state.Transfer.recapType,
  virementId: state.Transfer.virementId,
  deviceId: state.Login.deviceId,
  virData: state.Transfer.virData,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(Transfer),
);
