import { actionCreator } from '../../utils/generators';
import BeneficiariesActionTypes from './beneficiaries.types';

export const getAllBeneficiaries = () => ({
  type: actionCreator('req', BeneficiariesActionTypes.GET_ALL_BENEFICIARIES),
});
export const getTransferBeneficiaries = () => ({
  type: actionCreator(
    'req',
    BeneficiariesActionTypes.GET_TRANSFER_BENEFICIARIES,
  ),
});
export const getCashTransferBeneficiaries = () => ({
  type: actionCreator(
    'req',
    BeneficiariesActionTypes.GET_CASH_TRANSFER_BENEFICIARIES,
  ),
});
export const deleteBeneficiary = beneficiaryId => ({
  type: actionCreator('req', BeneficiariesActionTypes.DELETE_BENEFICIARY),
  payload: beneficiaryId,
});
export const editBeneficiary = beneficiary => ({
  type: actionCreator('req', BeneficiariesActionTypes.EDIT_BENEFICIARY),
  payload: beneficiary,
});
export const addBeneficiary = beneficiary => ({
  type: actionCreator('req', BeneficiariesActionTypes.ADD_BENEFICIARY),
  payload: beneficiary,
});
export const addTransferBeneficiary = beneficiary => ({
  type: actionCreator('req', BeneficiariesActionTypes.ADD_TRANSFER_BENEFICIARY),
  payload: beneficiary,
});
export const addCashTransferBeneficiary = beneficiary => ({
  type: actionCreator(
    'req',
    BeneficiariesActionTypes.ADD_CASH_TRANSFER_BENEFICIARY,
  ),
  payload: beneficiary,
});
