import React from 'react';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';

const OTPRecapForm = props => (
  <div className="otp-form">
    <div className="opt-form_prez">
      <h5 className="d-inline sm-pr-10">
        {t('Components:smsSent')}
        <span>{props.ownNumber}</span> {t('Components:containingTheCodeToType')}
      </h5>
      <button
        className="btn btn-space btn-default btn-sm sm-mt-5"
        onClick={props.onclickbutton}
      >
        <span>{t('Components:resendCode')}</span>
        <i className="c-fmlnet__add" />
      </button>
    </div>
    <div className="align-items-center row">
      <label className="col-sm-3 control-label text-right">
        {t('Components:typeCode')}
      </label>
      <div className="col-sm-9">
        <input
          className="form-control"
          type="text"
          onChange={props.onCodeChange}
          value={props.codeValue}
        />
      </div>
    </div>
  </div>
);

export default withTranslation()(OTPRecapForm);
