import React from 'react';
import { connect } from 'react-redux';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import { resetCashTransferStepper } from '../../redux/cashTransfer/cashTransfer.actions';
import { resetTransferStepper } from '../../redux/transfer/transfer.actions';

//line style
const CustomConnector = withStyles({
  alternativeLabel: {
    top: 22,
  },
  active: {
    '& $line': {
      backgroundImage: 'linear-gradient( 95deg,#622800 0%,#622800 100%)',
    },
  },
  completed: {
    '& $line': {
      backgroundImage: 'linear-gradient( 95deg,#622800 0%,#622800 100%)',
    },
  },
  line: {
    height: 3,
    border: 1,
    backgroundColor: '#ccc',
    borderRadius: 1,
  },
})(StepConnector);

//circle style
const useCustomStepIconComponentStyles = makeStyles({
  root: {
    backgroundColor: '#ccc',
    zIndex: 1,
    color: '#fff',
    width: 50,
    height: 50,
    display: 'flex',
    borderRadius: '50%',
    justifyContent: 'center',
    alignItems: 'center',
  },
  active: {
    backgroundImage: 'linear-gradient( 95deg,#622800 0%,#622800 100%)',
    boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)',
  },
  completed: {
    backgroundImage: 'linear-gradient( 95deg,#622800 0%,#622800 100%)',
  },
});

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
}));

const AbiStepper = props => {
  let activeStepInStore = null;
  props.CashtransferStepper
    ? (activeStepInStore = props.cashTransferactiveStep)
    : (activeStepInStore = props.TransferactiveStep);
  let stepsTitles = props.elements.map(data => data.stepTitle);
  const classes = useStyles();
  let startingStep = props.fromFavorites ? props.startingStep : 1;

  const [activeStep, setActiveStep] = React.useState(startingStep - 1);

  const CustomStepIconComponent = props => {
    const classes = useCustomStepIconComponentStyles();
    const { active, completed } = props;
    return (
      <div
        className={clsx(classes.root, {
          [classes.active]: active,
          [classes.completed]: completed,
        })}
      >
        {_getStepIcon(props.icon)}
      </div>
    );
  };

  const _getCurrentStep = stepNumber =>
    props.elements.filter(
      step => String(step.stepNumber) === String(stepNumber),
    )[0];

  const _getStepIcon = iconNumber => {
    let currentStep = _getCurrentStep(iconNumber - 1);
    return currentStep.stepIcon;
  };

  const _getStepRender = stepNumber => {
    let currentStep = _getCurrentStep(stepNumber);
    return currentStep.stepRender;
  };

  const _handleNext = e => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const _handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const _handleReset = () => {
    setActiveStep(0);
    props.resetCashTransferStepper();
    props.resetTransferStepper();
  };
  if (
    (activeStepInStore === 2 && activeStep !== activeStepInStore) ||
    (activeStepInStore === 3 && activeStep !== activeStepInStore)
  ) {
    setActiveStep(activeStepInStore);
  }
  return (
    <div className={classes.root}>
      <Stepper
        alternativeLabel
        activeStep={activeStep}
        connector={<CustomConnector />}
      >
        {stepsTitles.map(label => (
          <Step key={label}>
            <StepLabel StepIconComponent={CustomStepIconComponent}>
              {label}
            </StepLabel>
          </Step>
        ))}
      </Stepper>
      <div>
        <div>
          <div>{_getStepRender(activeStep)}</div>
          <div className="panel-footer">
            <div className="form-cta sm-pt-15 sm-pb-15 sm-mt-30">
              <div className="text-right sm-pr-30">
                {activeStep === stepsTitles.length - 1 ? (
                  <button
                    className="btn btn-space btn-rounded btn-secondary"
                    disabled={activeStep !== stepsTitles.length - 1}
                    onClick={_handleReset}
                  >
                    {t('Components:finish')}
                  </button>
                ) : (
                  <>
                    {activeStep === stepsTitles.length - 2 ? (
                      <button
                        className="btn btn-space btn-rounded btn-secondary"
                        disabled={activeStep === 0}
                        onClick={_handleReset}
                      >
                        {t('Components:cancel')}{' '}
                      </button>
                    ) : (
                      <button
                        className="btn btn-space btn-rounded btn-secondary"
                        disabled={activeStep === 0}
                        onClick={_handleBack}
                      >
                        {' '}
                        {t('Components:back')}{' '}
                      </button>
                    )}
                    {activeStep === 0 ? (
                      <button
                        className="btn btn-space btn-rounded btn-secondary"
                        disabled={props.dis1}
                        onClick={_handleNext}
                      >
                        {t('Components:next')}
                      </button>
                    ) : activeStep === stepsTitles.length - 2 ? (
                      <button
                        className="btn btn-space btn-rounded btn-secondary"
                        disabled={props.disableStep3}
                        onClick={e => {
                          props.ValidateAction();
                        }}
                      >
                        {t('Components:validate')}
                      </button>
                    ) : (
                      <button
                        className="btn btn-space btn-rounded btn-secondary"
                        disabled={props.disableStep2}
                        onClick={e => {
                          props.action2();
                        }}
                      >
                        {t('Components:next')}
                      </button>
                    )}
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
const mapDispatchToProps = dispatch => ({
  resetCashTransferStepper: () => {
    dispatch(resetCashTransferStepper());
  },
  resetTransferStepper: () => {
    dispatch(resetTransferStepper());
  },
});
const mapStateToProps = state => ({
  cashTransferactiveStep: state.CashTransfer.activeStep,
  TransferactiveStep: state.Transfer.activeStep,
});
export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(AbiStepper),
);
