import { connect } from 'react-redux';
import Transfers from './components/feature.jsx';
import { activateTransfersTab } from '../../redux/menu/menu.actions.js';

const mapStateToProps = state => ({
  activeTab: state.Menu.transfersActiveTab,
});
const mapDispatchToProps = dispatch => ({
  activateTab: activeTab => {
    dispatch(activateTransfersTab(activeTab));
  },
});
export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Transfers);
