import React from 'react';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import Panel from '../../../components/Panel/Panel';
import BeneficiariesTable from '../../../components/DataTable/CustomDataTable.jsx';
import {
  getAllBeneficiaries,
  deleteBeneficiary,
} from '../../../redux/beneficiaries/beneficiaries.actions';
import { BENEFICIARIES_NAMESPACE } from '../constants';
import {
  showMessage,
  hideMessage,
} from '../../../redux/messages/messages.actions';
import HandleBeneficiaryModal from './HandleBeneficiaryModal';
import { HandleBeneficiaryMode } from './BeneficiaryHandlerHelpers';

const ActionsButtonsColumn = props => (
  <div className="action-btn">
    <button className="btn btn-secondary btn-sm" onClick={props.onDelete}>
      <span className="actions_btn md-trigger popin_btn remove" />
    </button>
    <button className="btn btn-clear text-alert btn-sm" onClick={props.onEdit}>
      <span className="design-button md-trigger popin_btn editer">
        {t(`${BENEFICIARIES_NAMESPACE}edit`)}
      </span>
    </button>
  </div>
);
class Beneficiaries extends React.Component {
  constructor() {
    super();
    this.state = {
      showHandleBeneficiaryModal: false,
      beneficiaryToEdit: null,
      handleBeneficiaryMode: null,
    };
    this.handleBeneficiaryFormRef = null;
  }
  componentDidMount() {
    this.props.getBeneficiaries();
  }
  _buildDataTableRows = () =>
    this.props.Beneficiaries.map(data => ({
      beneficiaryId: data.beneficiaryId,
      name: data.name,
      type: data.type,
      firstName: data.firstName,
      lastName: data.lastName,
      mobileNumber: data.mobileNumber === null ? '' : data.mobileNumber,
      bankCode: data.bankCode,
      atmCode: data.atmCode,
      accountNumber: data.accountNumber,
      ribKey: data.ribKey,
      icon: data.icon,
      scope: data.scope,
    }));
  _deleteBeneficiary = beneficiaryId => {
    this.props.deleteBeneficiary(beneficiaryId);
    this.props.hideMessage();
  };
  _toggleHandleBeneficiaryModal = (beneficiary, handleBeneficiaryMode) => {
    this.setState({
      showHandleBeneficiaryModal: !this.state.showHandleBeneficiaryModal,
      beneficiaryToEdit: beneficiary,
      handleBeneficiaryMode: handleBeneficiaryMode,
    });
  };
  _submitHandleBeneficiary = () => {
    this.handleBeneficiaryFormRef.submitForm();
  };
  _setHandleBeneficiaryFormRef = ref => {
    this.handleBeneficiaryFormRef = ref;
  };
  _actionTemplate = beneficiary => (
    <ActionsButtonsColumn
      onDelete={() => {
        this.props.showMessage(
          t(`${BENEFICIARIES_NAMESPACE}deletionConfirm`),
          'WARN',
          t(`${BENEFICIARIES_NAMESPACE}confirm`),
          true,
          () => this._deleteBeneficiary(beneficiary.beneficiaryId),
        );
      }}
      onEdit={() =>
        this._toggleHandleBeneficiaryModal(
          beneficiary,
          HandleBeneficiaryMode.EDIT,
        )
      }
    />
  );
  render() {
    let rows = this._buildDataTableRows();
    return (
      <div className="row mx-auto">
        <div className="col-sm-12">
          <Panel
            title={t(`${BENEFICIARIES_NAMESPACE}beneficiariesList`)}
            hasButton
            buttonAction={() =>
              this._toggleHandleBeneficiaryModal(
                null,
                HandleBeneficiaryMode.ADD,
              )
            }
            buttonText={t(`${BENEFICIARIES_NAMESPACE}addBeneficiary`)}
          >
            <BeneficiariesTable
              value={rows}
              onRowReorder={e => (rows = e.value)}
              reference={el => (this.dt = el)}
              columnResizeMode="fit"
              cols={[
                {
                  field: 'name',
                  header: t(`${BENEFICIARIES_NAMESPACE}beneficiaryName`),
                },
                {
                  field: 'accountNumber',
                  header: t(`${BENEFICIARIES_NAMESPACE}accountNumber`),
                },
                {
                  field: 'mobileNumber',
                  header: t(`${BENEFICIARIES_NAMESPACE}mobileNumber`),
                },
                {
                  field: 'type',
                  header: t(`${BENEFICIARIES_NAMESPACE}type`),
                },
                {
                  field: 'action',
                  header: t(`${BENEFICIARIES_NAMESPACE}actionLabel`),
                  body: this._actionTemplate,
                },
              ]}
            />
          </Panel>
          {this.state.showHandleBeneficiaryModal ? (
            <HandleBeneficiaryModal
              show={this.state.showHandleBeneficiaryModal}
              handleClose={this._toggleHandleBeneficiaryModal}
              handleValidate={this._submitHandleBeneficiary}
              beneficiaryFormRefSetter={this._setHandleBeneficiaryFormRef}
              beneficiary={this.state.beneficiaryToEdit}
              mode={this.state.handleBeneficiaryMode}
            />
          ) : null}
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getBeneficiaries: () => {
    dispatch(getAllBeneficiaries());
  },
  showMessage: (
    text,
    severity,
    validateTitle,
    hasCancelBtn,
    handleValidate,
  ) => {
    dispatch(
      showMessage(text, severity, validateTitle, hasCancelBtn, handleValidate),
    );
  },
  hideMessage: () => {
    dispatch(hideMessage());
  },
  deleteBeneficiary: beneficiaryId => {
    dispatch(deleteBeneficiary(beneficiaryId));
  },
});

const mapStateToProps = state => ({
  Beneficiaries: state.Beneficiaries.allBeneficiaries,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(Beneficiaries),
);
