import React from 'react';
import { withTranslation } from 'react-i18next';
import { t } from '../../../i18n';
import RecapPanelContainer from '../RecapPanelContainer/RecapPanelContainer';

const ValidationRecap = props => (
  <RecapPanelContainer
    title={t('Components:confirmCashTransfertToGAB')}
    subtitle={props.title}
  >
    <div className="panel-body">
      <ul>
        <li>
          {t('Components:beneficiaryName')}
          <span>{props.name}</span>
        </li>
        <li>
          {t('Components:beneficiaryPhoneNumber')}
          <span>{props.number}</span>
        </li>
        <li>
          {t('Components:transfertAmount')}
          <span>
            {props.amount}
            <strong className="currency"> {t('Components:currency')}</strong>
          </span>
        </li>
        {props.cashTransfert ? (
          <li>
            {t('Components:withdrawalcode')}
            <span>{props.cashTransferData.executionReference}</span>
          </li>
        ) : null}
      </ul>
    </div>
  </RecapPanelContainer>
);

export default withTranslation()(ValidationRecap);
