import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import PageHeader from '../../../components/PageHeader/PageHeader';
import PageHeaderNav from '../../../components/PageHeadNav/PageHeadNav';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import Transfer from './Transfer';
import CashTransferHistory from './CashTransferHistory';
import CashTransfer from './CashTransfer';
import Favorites from './Favorites';
import { TRANSFERS_NAMESPACE, TABS } from '../constants';
import TransferHistory from './TransferHistory';
import Beneficiaries from './Beneficiaries';

class Feature extends Component {
  _activateTab = tabId => {
    this.props.activateTab(tabId);
  };
  _getTabTitle = () => {
    let tab = TABS.filter(tab => tab.id === this.props.activeTab)[0];
    return tab.title;
  };
  renderActiveTab = () => {
    switch (this.props.activeTab) {
      case 'transfer':
        return (
          <>
            <Transfer />
            <div className="sm-mt-40 sm-mb-40" />
            <TransferHistory />
          </>
        );
      case 'favorites':
        return <Favorites />;
      case 'cashTransfer':
        return (
          <>
            <CashTransfer />
            <div className="sm-mt-40 sm-mb-40"></div>
            <CashTransferHistory />
          </>
        );
      case 'history':
        return (
          <>
            <TransferHistory />
            <div className="sm-mt-40 sm-mb-40"></div>
            <CashTransferHistory />
          </>
        );
      case 'beneficiaries':
        return (
          <>
            <Beneficiaries />
          </>
        );
      default:
        return (
          <>
            <Transfer />
            <div className="sm-mt-40 sm-mb-40"></div>
            <TransferHistory />
          </>
        );
    }
  };

  render() {
    return (
      <div className="page-content">
        <div className="page-content_side">
          <PageHeader text={t(this._getTabTitle())}>
            <li key="1">
              <Link to="/dashboard">{t(`${TRANSFERS_NAMESPACE}homePage`)}</Link>
            </li>
            <li key="2">
              <Link to="/transfers">
                {t(`${TRANSFERS_NAMESPACE}breadCrumbSecondLink`)}
              </Link>
            </li>
            <li className="active" key="3">
              {t(this._getTabTitle())}
            </li>
          </PageHeader>
          <div className="page-content_inner">
            <PageHeaderNav>
              {TABS.map(tab => (
                <li key={tab.id}>
                  <Link
                    className={tab.id === this.props.activeTab ? 'active' : ''}
                    title={t(tab.title)}
                    to="#"
                    onClick={() => this._activateTab(tab.id)}
                  >
                    {t(tab.title)}
                  </Link>
                </li>
              ))}
            </PageHeaderNav>
            <div className="main-content container-fluid">
              {this.renderActiveTab()}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default withTranslation()(Feature);
