import React from 'react';
import { connect } from 'react-redux';
import PasswordRecapForm from '../RecapForm/PasswordRecapForm';
import OTPRecapForm from './OTPRecapForm';

const RecapForm = props => {
  switch (props.transferRecapType) {
    case 'PASSWORD':
      return <PasswordRecapForm {...props} />;
    case 'OTP':
      return <OTPRecapForm {...props} />;
    default:
      return <PasswordRecapForm {...props} />;
  }
};
const mapStateToProps = state => ({
  transferRecapType: state.Transfer.recapType,
});

export default connect(
  mapStateToProps,
  null,
)(RecapForm);
