import React from 'react';
import Stepper from '../../../components/Stepper/Stepper';
import MontantStep from '../../../components/MontantForm/MontantFormStep';
import RecapStep from '../../../components/Recap/RecapStep';
import BeneficiaryStepIcon from '../../../assets/SvgComponents/StepperIcons/BeneficiaryStepIcon';
import AmountStepIcon from '../../../assets/SvgComponents/StepperIcons/AmountStepIcon';
import RecapStepIcon from '../../../assets/SvgComponents/StepperIcons/RecapStepIcon';
import ConfirmationStepIcon from '../../../assets/SvgComponents/StepperIcons/ConfirmationStepIcon';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { getAccounts } from '../../../redux/accounts/accounts.actions';
import {
  initCashTransfer,
  validateCashTransfer,
  resendOtpCashTransfer,
  passwordValidateCashTransfer,
} from '../../../redux/cashTransfer/cashTransfer.actions';
import BeneficiaryStep from '../../../components/Beneficiaries/BeneficiariesStep';
import { CASH_TRANSFER_NAMESPACE } from '../constants';
import { getCashTransferBeneficiaries } from '../../../redux/beneficiaries/beneficiaries.actions';
import HandleBeneficiaryModal from './HandleBeneficiaryModal';
import { HandleBeneficiaryMode } from './BeneficiaryHandlerHelpers';
import ValidationRecap from 'components/Recap/RecapForm/ValidationRecap';
import { showMessage } from '../../../redux/messages/messages.actions';
import { setToDefaultState } from 'redux/transfer/transfer.actions';

const user = sessionStorage
  .getItem('SessionUserIndex')
  .concat(sessionStorage.getItem('SessionUserPhone'));
class CashTransfer extends React.Component {
  addBeneficiaryFormRef = null;
  state = {
    label: '',
    amount: '',
    favoris: false,
    codeValue: '',
    pinValue: '',
    selectedAccountId: '',
    selectedBeneficiaryId: '',
    selectedAccount: {},
    selectedBeneficiary: {},
    showAddBeneficiaryModal: false,
  };
  componentWillUnmount() {
    this.props.setToDefaultState();
  }
  componentDidUpdate(prevProps, prevState, snapshot) {
    if (
      prevProps.cashTransferData !== this.props.cashTransferData &&
      this.props.cashTransferData
    ) {
      this.props.showMessage(
        t(`${CASH_TRANSFER_NAMESPACE}cashTransferIsDone`),
        'CONFIRM',
        t('Components:validate'),
        false,
      );
    }
  }
  _toggleAddBeneficiaryModal = () => {
    this.setState({
      showAddBeneficiaryModal: !this.state.showAddBeneficiaryModal,
    });
  };
  _submitAddBeneficiary = () => {
    this.addBeneficiaryFormRef.submitForm();
  };
  _setAddBeneficiaryFormRef = ref => {
    this.addBeneficiaryFormRef = ref;
  };
  _accountChangeHandler = accountId => {
    this.setState({ selectedAccountId: accountId });
  };
  _BeneficiaryChangeHandler = beneficiaryId => {
    this.setState({ selectedBeneficiaryId: beneficiaryId });
  };
  _amountChangeHandler = amount => {
    this.setState({ amount: amount });
  };
  _labelChangeHandler = label => {
    this.setState({ label: label });
  };

  componentDidMount() {
    this.props.getAccounts();
    this.props.getCashTransferBeneficiaries();
  }

  _favorisHandler = childData => {
    this.setState({ favoris: childData });
  };
  _pinHandler = childData => {
    this.setState({ pinValue: childData });
  };

  handleChange = event => {
    this.setState({ codeValue: event.target.value });
  };
  handlePinChange = event => {
    this.setState({ pinValue: event.target.value });
  };
  submit1Handler = () => {
    let selectedClientAccount = this.props.accounts.filter(
      account => account.clientAccountId === this.state.selectedAccountId,
    );

    let selectedBeneficiary = this.props.cashTransferBeneficiaries.filter(
      beneficiary =>
        beneficiary.beneficiaryId === this.state.selectedBeneficiaryId,
    );

    this.setState(
      {
        selectedAccount: selectedClientAccount[0],
        selectedBeneficiary: selectedBeneficiary[0],
      },
      () =>
        this.props.init(
          this.state.selectedAccount,
          this.state.selectedBeneficiary,
          this.state.label,
          this.state.amount,
          this.state.label,
          this.state.favoris,
        ),
    );
  };
  submit2Handler = () => {
    this.props.passwordValidate(
      this.state.codeValue,
      this.state.favoris,
      user,
      this.props.typeValidation,
      this.state.pinValue,
      this.props.atmTranfertId,
    );
  };

  submit3Handler = () => {
    this.props.resendOtp(this.props.otpId);
  };
  render() {
    let fromFavorites = false;
    var favorite = null;
    if (this.props.activeTabParams !== undefined) {
      fromFavorites = this.props.activeTabParams.fromFavorites;
      favorite = this.props.favorites.filter(
        transaction =>
          transaction.transactionType === 'cashTransfer' &&
          transaction.transactionId ===
            this.props.activeTabParams.transactionId,
      )[0];
    }
    let user = sessionStorage
      .getItem('SessionUserIndex')
      .concat(sessionStorage.getItem('SessionUserPhone'));
    return (
      <div className="row">
        <div className="col-sm-12">
          <div className="wizard-row">
            <div className="panel panel-default panel-wizard">
              <Stepper
                CashtransferStepper
                fromFavorites={fromFavorites}
                startingStep={fromFavorites ? 2 : 1}
                elements={[
                  {
                    stepNumber: 0,
                    stepTitle: t(
                      `${CASH_TRANSFER_NAMESPACE}beneficiaryStepTitle`,
                    ),
                    stepIcon: <BeneficiaryStepIcon />,
                    stepRender: (
                      <BeneficiaryStep
                        handleBeneficiary={this._toggleAddBeneficiaryModal}
                        selectedAccount={
                          fromFavorites && favorite !== null
                            ? favorite.clientAccount.clientAccountId
                            : this.state.selectedAccountId
                        }
                        selectedBeneficiary={
                          fromFavorites && favorite !== null
                            ? favorite.beneficiary.beneficiaryId
                            : this.state.selectedBeneficiaryId
                        }
                        accountsList={this.props.accounts}
                        onAccountChange={this._accountChangeHandler}
                        showAlertEnabled={
                          this.props.cashTransferBeneficiaries.length === 0
                        }
                        onBeneficiaryChange={this._BeneficiaryChangeHandler}
                        beneficiariesList={this.props.cashTransferBeneficiaries}
                      />
                    ),
                  },
                  {
                    stepNumber: 1,
                    stepTitle: t(`${CASH_TRANSFER_NAMESPACE}amountStepTitle`),
                    stepIcon: <AmountStepIcon />,
                    stepRender: (
                      <MontantStep
                        amount={
                          fromFavorites && favorite !== null
                            ? favorite.amount
                            : this._amountChangeHandler
                        }
                        label={
                          fromFavorites && favorite !== null
                            ? favorite.description
                            : this._labelChangeHandler
                        }
                        isFavorite={
                          fromFavorites && favorite !== null
                            ? true
                            : this._favorisHandler
                        }
                        favoriteLabel={
                          fromFavorites && favorite !== null
                            ? favorite.label
                            : ''
                        }
                        pin={this._pinHandler}
                        pinField
                      />
                    ),
                  },
                  {
                    stepNumber: 2,
                    stepTitle: t(`${CASH_TRANSFER_NAMESPACE}recapStepTitle`),
                    stepIcon: <RecapStepIcon />,
                    stepRender: (
                      <RecapStep
                        title={t(
                          'Components:youHaveAskedForaCashtransfertToGAB',
                        )}
                        name={
                          fromFavorites && favorite !== null
                            ? favorite.beneficiary.name
                            : this.state.selectedBeneficiary.name
                        }
                        number={
                          fromFavorites && favorite !== null
                            ? favorite.beneficiary.mobileNumber
                            : this.state.selectedBeneficiary.mobileNumber
                        }
                        amount={
                          fromFavorites && favorite !== null
                            ? favorite.amount
                            : this.state.amount
                        }
                        ownNumber={user}
                        onclickbutton={this.submit3Handler}
                        onCodeChange={this.handleChange}
                        codeValue={this.state.codeValue}
                      />
                    ),
                  },
                  {
                    stepNumber: 3,
                    stepTitle: t(
                      `${CASH_TRANSFER_NAMESPACE}confirmationStepTitle`,
                    ),
                    stepIcon: <ConfirmationStepIcon />,
                    stepRender: (
                      <ValidationRecap
                        title={t('Components:youHaveMadedACashtransfertToGAB')}
                        name={
                          fromFavorites && favorite !== null
                            ? favorite.beneficiary.name
                            : this.state.selectedBeneficiary.name
                        }
                        number={
                          fromFavorites && favorite !== null
                            ? favorite.beneficiary.mobileNumber
                            : this.state.selectedBeneficiary.mobileNumber
                        }
                        amount={
                          fromFavorites && favorite !== null
                            ? favorite.amount
                            : this.state.amount
                        }
                        ownNumber={user}
                        onclickbutton={this.submit3Handler}
                        onCodeChange={this.handleChange}
                        codeValue={this.state.codeValue}
                        cashTransfert={true}
                        cashTransferData={this.props.cashTransferData}
                      />
                    ),
                  },
                ]}
                dis1={
                  !fromFavorites &&
                  (this.state.selectedAccountId === '' ||
                    this.state.selectedBeneficiaryId === '')
                }
                dis2={
                  fromFavorites
                    ? this.state.pinValue === ''
                    : this.state.amount === '' || this.state.label === ''
                }
                dis3={this.state.codeValue === ''}
                resetStats={() => {
                  this.setState({
                    selectedAccount: {},
                    selectedBeneficiary: {},
                    selectedAccountId: '',
                    selectedBeneficiaryId: '',
                    amount: '',
                    label: '',
                    codeValue: '',
                  });
                }}
                action2={this.submit1Handler}
                ValidateAction={this.submit2Handler}
                ModalBackComp={
                  <BeneficiaryStep
                    accountsList={this.props.accounts}
                    onAccountChange={this._accountChangeHandler}
                    onBeneficiaryChange={this._BeneficiaryChangeHandler}
                    beneficiariesList={this.props.cashTransferBeneficiaries}
                  />
                }
                success={this.props.error === null}
                isLoading={this.props.isLoading}
              />
              {this.state.showAddBeneficiaryModal ? (
                <HandleBeneficiaryModal
                  show={this.state.showAddBeneficiaryModal}
                  handleClose={this._toggleAddBeneficiaryModal}
                  handleValidate={this._submitAddBeneficiary}
                  beneficiaryFormRefSetter={this._setAddBeneficiaryFormRef}
                  mode={HandleBeneficiaryMode.ADD_CASH_TRANSFER}
                />
              ) : null}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  getCashTransferBeneficiaries: () => {
    dispatch(getCashTransferBeneficiaries());
  },
  showMessage: (text, severity, validateTitle, hasCancelBtn) => {
    dispatch(showMessage(text, severity, validateTitle, hasCancelBtn));
  },
  passwordValidate: (
    cricket,
    favoris,
    smiley,
    typeValidation,
    pin,
    atmTransfertId,
  ) => {
    dispatch(
      passwordValidateCashTransfer({
        cricket,
        favoris,
        smiley,
        typeValidation,
        pin,
        atmTranfertId: atmTransfertId,
      }),
    );
  },
  getAccounts: () => {
    dispatch(getAccounts());
  },
  setToDefaultState: () => {
    dispatch(setToDefaultState());
  },
  init: (clientAccount, beneficiary, label, amount, description, favoris) => {
    dispatch(
      initCashTransfer({
        clientAccount,
        beneficiary,
        label,
        amount,
        description,
        favoris,
      }),
    );
  },
  validate: (otpId, otpValue, favoris, pin) => {
    dispatch(
      validateCashTransfer({
        otpId,
        otpValue,
        favoris,
        pin,
      }),
    );
  },
  resendOtp: otpId => {
    dispatch(
      resendOtpCashTransfer({
        otpId,
      }),
    );
  },
});

const mapStateToProps = state => ({
  cashTransferBeneficiaries: state.Beneficiaries.cashTransferBeneficiaries,
  accounts: state.Accounts.accounts,
  isLoading: state.CashTransfer.isLoading,
  error: state.CashTransfer.error,
  userName: state,
  favorites: state.Favorites.favorites,
  activeTabParams: state.Menu.activeTabParams,
  otpId: state.CashTransfer.otpId,
  atmTranfertId: state.CashTransfer.atmTranfertId,
  typeValidation: state.CashTransfer.recapType,
  cashTransferData: state.CashTransfer.cashTransferData,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(CashTransfer),
);
