import React from 'react';
import PanelContainer from './RecapPanelContainer/RecapPanelContainer';
import RecapForm from './RecapForm/RecapForm';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';

function RecapStep(props) {
  return (
    <PanelContainer
      title={t('Components:confirmCashTransfertToGAB')}
      subtitle={props.title}
    >
      <div className="panel-body">
        <ul>
          <li>
            {t('Components:beneficiaryName')}
            <span>{props.name}</span>
          </li>
          <li>
            {t('Components:beneficiaryPhoneNumber')}
            <span>{props.number}</span>
          </li>
          <li>
            {t('Components:transfertAmount')}
            <span>
              {props.amount}
              <strong className="currency"> {t('Components:currency')}</strong>
            </span>
          </li>
          {props.cashTransfert ? (
            <li>
              {t('Components:withdrawalcode')}
              <span>{props.code}</span>
            </li>
          ) : null}
        </ul>
        <RecapForm
          ownNumber={props.ownNumber}
          onclickbutton={props.onclickbutton}
          onCodeChange={props.onCodeChange}
          codeValue={props.codeValue}
          onPinChange={props.onPinChange}
          pinValue={props.pinValue}
          pin={props.pin}
        />
      </div>
    </PanelContainer>
  );
}

export default withTranslation()(RecapStep);
