import React from 'react';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import classes from './Montant.module.css';
import ExpiringFormFieldError from '../../components/Ui/FormFieldError/ExpiringFormFieldError';

const pinRegExp = new RegExp(`/^[0-9]{4}$/`);
const validationSchema = Yup.object().shape({
  amount: Yup.number()
    .typeError(t('Components:amountRequired'))
    .positive()
    .nullable(true)
    .required(t('Components:amountRequired')),
  description: Yup.string().required(t('Components:labelRequired')),
  pinCode: Yup.string()
    .required(t('Components:pinRequired'))
    .matches(pinRegExp, t('Components:pinLength')),
});

const invalidInputClasses = `form-control ${classes.Invalid}`;
const MontantForm = props => (
  <div className="panel-body">
    <div className="wizard-progress_content">
      <div className="wizard-progress_step step-content active">
        <div className="row mx-auto">
          <div className="col-sm-12">
            <Formik
              initialValues={{
                amount: props.amount,
                label: props.label,
                addFavoris: props.isFavorite,
                favorisLabel: props.favoriteLabel,
                pinCode: '',
              }}
              validationSchema={validationSchema}
              onSubmit={(values, { setSubmitting }) => {
                setSubmitting(true);
              }}
            >
              {({
                values,
                errors,
                touched,
                handleChange,
                handleBlur,
                handleSubmit,
                isSubmitting,
              }) => (
                <form
                  className="form-horizontal group-border-dashed"
                  onSubmit={handleSubmit}
                  style={{ borderRadius: '0px' }}
                >
                  <div className="form-group align-items-center justify-content-center">
                    <label className="col-sm-12 col-md-3 col-lg-2 control-label">
                      {t('Components:amount')}
                    </label>
                    <div className="col-sm-12 col-md-7 col-lg-3">
                      <input
                        className={
                          errors.amount ? invalidInputClasses : 'form-control'
                        }
                        name="amount"
                        type="number"
                        onChange={e => {
                          handleChange(e);
                          props.amount(e.target.value);
                        }}
                        onBlur={handleBlur}
                        value={values.amount}
                      />
                      <ExpiringFormFieldError name="amount" />
                    </div>
                  </div>
                  <div className="form-group align-items-center justify-content-center">
                    <label className="col-sm-12 col-md-3 col-lg-2 control-label">
                      {t('Components:label')}
                    </label>
                    <div className="col-sm-12 col-md-7 col-lg-3">
                      <input
                        className={
                          errors.description
                            ? invalidInputClasses
                            : 'form-control'
                        }
                        name="description"
                        type="text"
                        placeholder=""
                        onChange={e => {
                          handleChange(e);
                          props.label(e.target.value);
                        }}
                        onBlur={handleBlur}
                        value={values.description}
                      />
                      <ExpiringFormFieldError name="description" />
                    </div>
                  </div>
                  {props.pinField ? (
                    <div className="form-group align-items-center justify-content-center">
                      <label className="col-sm-12 col-md-3 col-lg-2 control-label">
                        {t('Components:pinCode')}
                      </label>
                      <div className="col-sm-12 col-md-7 col-lg-3">
                        <input
                          className={
                            errors.pinCode
                              ? invalidInputClasses
                              : 'form-control'
                          }
                          name="pinCode"
                          type="text"
                          onChange={e => {
                            handleChange(e);
                            props.pin(e.target.value);
                          }}
                          onBlur={handleBlur}
                          value={values.pinCode}
                        />
                        <ExpiringFormFieldError name="pinCode" />
                      </div>
                    </div>
                  ) : null}
                  <div className="form-group align-items-center ">
                    <div className="col-sm-12 col-md-5 col-lg-5 text-right sm-text-left">
                      <label>{t('Components:addFavoris')}</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-4">
                      <div className="check-field_action check-inline">
                        <div className="bcp-checkbox">
                          <input
                            name="addFavoris"
                            id="remember"
                            type="checkbox"
                            onChange={e => {
                              handleChange(e);
                              props.isFavorite(e.target.checked);
                            }}
                            onBlur={handleBlur}
                            value={values.addFavoris}
                            checked={values.addFavoris}
                          />
                          <label htmlFor="remember" />
                        </div>

                        <div className="d-inline-block check_favori col-sm-12">
                          <input
                            className="form-control"
                            type="text"
                            name="favorisLabel"
                            placeholder={t('Components:favorisLabel')}
                            onChange={handleChange}
                            onBlur={handleBlur}
                            value={values.favorisLabel}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
              )}
            </Formik>
          </div>
        </div>
      </div>
    </div>
  </div>
);

export default withTranslation()(MontantForm);
