import * as Yup from 'yup';
import { t } from '../../../i18n';
import { BENEFICIARIES_NAMESPACE } from '../constants';

export const BeneficiaryScope = {
  INTERN_BENEFICIARY: 'beneficiary_intern',
  EXTERN_BENEFICIARY: 'beneficiary_extern',
  CASH_TRANSFER: 'beneficiary_transfer_cash',
};

export const HandleBeneficiaryMode = {
  EDIT: 'EDIT',
  ADD: 'ADD',
  ADD_TRANSFER: 'ADD_TRANSFER',
  ADD_CASH_TRANSFER: 'ADD_CASH_TRANSFER',
};

export const renderValidationSchema = (mode, beneficiaryScope) => {
  let validationSchema = null;

  switch (mode) {
    case HandleBeneficiaryMode.EDIT:
    case HandleBeneficiaryMode.ADD: {
      validationSchema =
        beneficiaryScope === BeneficiaryScope.INTERN_BENEFICIARY
          ? Yup.object().shape({
              lastName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredLastName`),
              ),
              firstName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredFirstName`),
              ),
              mobileNumber: Yup.string()
                .test(
                  'mobile number validation',
                  t(
                    `${BENEFICIARIES_NAMESPACE}requiredAccountNumberOrMobileNumber`,
                  ),
                  function(value) {
                    const { accountNumber } = this.parent;
                    if (!accountNumber) return value !== undefined;
                    return true;
                  },
                )
                .matches(
                  /^[+]\d{11,12}$/,
                  t(`${BENEFICIARIES_NAMESPACE}invalidMobileNumber`),
                ),
              accountNumber: Yup.string()
                .test(
                  'account number validation',
                  t(
                    `${BENEFICIARIES_NAMESPACE}requiredAccountNumberOrMobileNumber`,
                  ),
                  function(value) {
                    const { mobileNumber } = this.parent;
                    if (!mobileNumber) return value !== undefined;
                    return true;
                  },
                )
                .matches(
                  /^\d+$/,
                  t(`${BENEFICIARIES_NAMESPACE}numericAccountNumber`),
                )
                .matches(
                  /^\d{11,12}$/,
                  t(
                    `${BENEFICIARIES_NAMESPACE}invalidInternAccountNumberLength`,
                  ),
                ),
            })
          : Yup.object().shape({
              lastName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredLastName`),
              ),
              firstName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredFirstName`),
              ),
              mobileNumber: Yup.string().matches(
                /^[+]\d{11,12}$/,
                t(`${BENEFICIARIES_NAMESPACE}invalidMobileNumber`),
              ),
              accountNumber: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredAccountNumber`))
                .matches(
                  /^\d+$/,
                  t(`${BENEFICIARIES_NAMESPACE}numericAccountNumber`),
                )
                .length(
                  12,
                  t(
                    `${BENEFICIARIES_NAMESPACE}invalidExternAccountNumberLength`,
                  ),
                ),
              atmCode: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredAtmCode`))
                .matches(/^\d+$/, t(`${BENEFICIARIES_NAMESPACE}numericAtmCode`))
                .length(5, t(`${BENEFICIARIES_NAMESPACE}invalidAtmCodeLength`)),
              bankCode: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredBankCode`))
                .length(
                  5,
                  t(`${BENEFICIARIES_NAMESPACE}invalidBankCodeLength`),
                ),
              ribKey: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredRibKey`))
                .matches(/^\d+$/, t(`${BENEFICIARIES_NAMESPACE}numericRibKey`))
                .length(2, t(`${BENEFICIARIES_NAMESPACE}invalidRibKeyLength`)),
            });
      break;
    }
    case HandleBeneficiaryMode.ADD_CASH_TRANSFER:
      validationSchema = Yup.object().shape({
        lastName: Yup.string().required(
          t(`${BENEFICIARIES_NAMESPACE}requiredLastName`),
        ),
        firstName: Yup.string().required(
          t(`${BENEFICIARIES_NAMESPACE}requiredFirstName`),
        ),
        mobileNumber: Yup.string()
          .required(t(`${BENEFICIARIES_NAMESPACE}requiredMobileNumber`))
          .matches(
            /^[+]\d{11,12}$/,
            t(`${BENEFICIARIES_NAMESPACE}invalidMobileNumber`),
          ),
      });
      break;
    case HandleBeneficiaryMode.ADD_TRANSFER:
      validationSchema =
        beneficiaryScope === BeneficiaryScope.INTERN_BENEFICIARY
          ? Yup.object().shape({
              lastName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredLastName`),
              ),
              firstName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredFirstName`),
              ),
              mobileNumber: Yup.string().matches(
                /^[+]\d{11,12}$/,
                t(`${BENEFICIARIES_NAMESPACE}invalidMobileNumber`),
              ),
              accountNumber: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredAccountNumber`))
                .matches(
                  /^\d+$/,
                  t(`${BENEFICIARIES_NAMESPACE}numericAccountNumber`),
                )
                .matches(
                  /^\d{11,12}$/,
                  t(
                    `${BENEFICIARIES_NAMESPACE}invalidInternAccountNumberLength`,
                  ),
                ),
            })
          : Yup.object().shape({
              lastName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredLastName`),
              ),
              firstName: Yup.string().required(
                t(`${BENEFICIARIES_NAMESPACE}requiredFirstName`),
              ),
              mobileNumber: Yup.string().matches(
                /^[+]\d{11,12}$/,
                t(`${BENEFICIARIES_NAMESPACE}invalidMobileNumber`),
              ),
              accountNumber: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredAccountNumber`))
                .matches(
                  /^\d+$/,
                  t(`${BENEFICIARIES_NAMESPACE}numericAccountNumber`),
                )
                .length(
                  12,
                  t(
                    `${BENEFICIARIES_NAMESPACE}invalidExternAccountNumberLength`,
                  ),
                ),
              atmCode: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredAtmCode`))
                .matches(/^\d+$/, t(`${BENEFICIARIES_NAMESPACE}numericAtmCode`))
                .length(5, t(`${BENEFICIARIES_NAMESPACE}invalidAtmCodeLength`)),
              bankCode: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredBankCode`))
                .length(
                  5,
                  t(`${BENEFICIARIES_NAMESPACE}invalidBankCodeLength`),
                ),
              ribKey: Yup.string()
                .required(t(`${BENEFICIARIES_NAMESPACE}requiredRibKey`))
                .matches(/^\d+$/, t(`${BENEFICIARIES_NAMESPACE}numericRibKey`))
                .length(2, t(`${BENEFICIARIES_NAMESPACE}invalidRibKeyLength`)),
            });

      break;
  }
  return validationSchema;
};
