import React from 'react';
import { styles } from './ExcelTemplate.styles';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import * as DateUtils from '../../utils/DateUtils';
import FormatsConfiguration from '../../utils/FormatsConfiguration';
import { HISTORY_NAMESPACE } from '../../features/Transfers/constants';
import AccountColumn from '../../components/AccountColumn/AccountColumn';

const TransfersHistoryTemplateExcel = props => {
  const { title, excelId } = props;
  const operations = props.tableRows.map(row => (
    <tr key={row.code} style={styles.table}>
      <td style={styles.firstColumn}>
        <p style={styles.firstColumnValue}>
          {DateUtils.formatDateTime(
            row.date,
            FormatsConfiguration.DateTimeFormat.fullDate,
          )}
        </p>
      </td>
      <td colSpan="2" style={styles.firstColumn}>
        <div style={styles.firstColumnValue}>
          <AccountColumn
            accountName={row.clientName}
            accountNumber={row.clientNum}
          />
        </div>
      </td>
      <td colSpan="2" style={styles.firstColumn}>
        <p style={styles.firstColumnValue}>{row.beneficiaryName}</p>
      </td>
      <td colSpan="2" style={styles.firstColumn}>
        <p style={styles.firstColumnValue}>{row.code}</p>
      </td>
      <td colSpan="4" style={styles.firstColumn}>
        <p style={styles.firstColumnValue}>
          {row.amount}
        </p>
      </td>
    </tr>
  ));

  return (
    <div className="excel-div" id={excelId} style={styles.pdf}>
      <div id="id1_1" style={styles.titleContainer}>
        <p style={styles.title}>{title}</p>
      </div>
      <div id="id1_2" style={styles.header}>
        <div style={styles.clientInfosContainer}>
          <p style={styles.clientInfos} />
        </div>
        <div style={styles.operationsPeriodContainer}>
          <p style={styles.operationPeriod} />
        </div>
      </div>
      <div style={styles.operations}>
        <div style={styles.operationsTableContainer}>
          <table cellPadding="0" cellSpacing="0" style={styles.table}>
            <thead>
              <tr>
                <td style={styles.dateOperationContainer}>
                  <p style={styles.dateOperation}>
                    {t(`${HISTORY_NAMESPACE}dateClnLabel`)}
                  </p>
                </td>
                <td colSpan="2" style={styles.dateValueContainer}>
                  <p style={styles.dateValue}>
                    {t(`${HISTORY_NAMESPACE}accountClnLabel`)}
                  </p>
                </td>
                <td colSpan="2" style={styles.referenceContainer}>
                  <p style={styles.reference}>
                    {t(`${HISTORY_NAMESPACE}beneficiaryClnLabel`)}
                  </p>
                </td>
                <td colSpan="2" style={styles.amountContainer}>
                  <p style={styles.amount}>
                    {t(`${HISTORY_NAMESPACE}referenceClnLabel`)}
                  </p>
                </td>
                <td colSpan="4" style={styles.labelContainer}>
                  <p style={styles.label}>
                    {t(`${HISTORY_NAMESPACE}amountClnLabel`)}
                  </p>
                </td>
              </tr>
            </thead>
            <tbody>{operations}</tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default withTranslation()(TransfersHistoryTemplateExcel);
