import React from 'react';
import { Formik } from 'formik';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import { BENEFICIARIES_NAMESPACE } from '../constants';
import FormModal from '../../../components/Ui/CustomModal/FormModal';
import { InputSwitch } from 'primereact/inputswitch';
import { connect } from 'react-redux';
import ExpiringFormFieldError from '../../../components/Ui/FormFieldError/ExpiringFormFieldError';
import classes from '../styles/Style.module.css';
import {
  editBeneficiary,
  addBeneficiary,
  addCashTransferBeneficiary,
  addTransferBeneficiary,
} from '../../../redux/beneficiaries/beneficiaries.actions';
import {
  BeneficiaryScope,
  HandleBeneficiaryMode,
  renderValidationSchema,
} from './BeneficiaryHandlerHelpers';

const inputClasses = `form-control ${classes.Invalid}`;

class HandleBeneficiaryModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      beneficiaryScope: BeneficiaryScope.INTERN_BENEFICIARY,
      showModal: this.props.show,
    };
  }
  _handleBeneficiary = (mode, beneficiary) => {
    beneficiary.name = `${beneficiary.lastName} ${beneficiary.firstName}`;
    beneficiary.type = 'physique';
    beneficiary.icon = null;
    if (this.state.beneficiaryScope === BeneficiaryScope.INTERN_BENEFICIARY) {
      beneficiary.atmCode = '';
      beneficiary.bankCode = '';
      beneficiary.ribKey = '';
      beneficiary.scope = BeneficiaryScope.INTERN_BENEFICIARY;
    } else {
      beneficiary.scope = BeneficiaryScope.EXTERN_BENEFICIARY;
    }
    if (beneficiary.accountNumber === '') {
      beneficiary.scope = BeneficiaryScope.CASH_TRANSFER;
    }
    if (beneficiary.accountNumber && beneficiary.accountNumber.length === 11) {
      beneficiary.accountNumber = '0'.concat(beneficiary.accountNumber);
    }
    switch (mode) {
      case HandleBeneficiaryMode.EDIT:
        this.props.editBeneficiary(beneficiary);
        break;
      case HandleBeneficiaryMode.ADD:
        if (!beneficiary.accountNumber) {
          beneficiary.scope = null;
          beneficiary.accountNumber = '';
        }
        this.props.addBeneficiary(beneficiary);
        break;
      case HandleBeneficiaryMode.ADD_TRANSFER:
        this.props.addTransferBeneficiary(beneficiary);
        break;
      case HandleBeneficiaryMode.ADD_CASH_TRANSFER:
        beneficiary.accountNumber = '';
        beneficiary.scope = BeneficiaryScope.CASH_TRANSFER;
        this.props.addCashTransferBeneficiary(beneficiary);
        break;
      default:
        break;
    }
    this.setState({ showModal: false });
  };
  componentDidMount() {
    if (this.props.beneficiary) {
      this.setState({ beneficiaryScope: this.props.beneficiary.scope });
    }
  }

  render() {
    return (
      <FormModal
        title={
          this.props.mode === HandleBeneficiaryMode.EDIT
            ? t(`${BENEFICIARIES_NAMESPACE}editBeneficiary`)
            : t(`${BENEFICIARIES_NAMESPACE}addBeneficiary`)
        }
        show={this.state.showModal}
        handleClose={this.props.handleClose}
        handleValidate={this.props.handleValidate}
        hasValidateBtn
      >
        <Formik
          ref={ref => this.props.beneficiaryFormRefSetter(ref)}
          initialValues={
            this.props.mode === HandleBeneficiaryMode.EDIT &&
            this.props.beneficiary
          }
          validationSchema={renderValidationSchema(
            this.props.mode,
            this.state.beneficiaryScope,
          )}
          onSubmit={(values, { setSubmitting }) => {
            setSubmitting(true);
            this._handleBeneficiary(this.props.mode, values);
          }}
        >
          {({ values, handleChange, handleSubmit, errors, handleBlur }) => (
            <div className="formulaire-benificiaire">
              <form onSubmit={handleSubmit}>
                <div className="form-row d-flex justify-content-between">
                  <div className="form-group col-md-6 col-sm-12">
                    <label
                      htmlFor="lastName"
                      className="col-sm-12 control-label"
                    >
                      {t(`${BENEFICIARIES_NAMESPACE}lastName`)}
                      <sup>*</sup>
                    </label>
                    <div className="col-sm-12">
                      <input
                        className={
                          errors.lastName ? inputClasses : 'form-control'
                        }
                        name="lastName"
                        type="text"
                        value={values.lastName}
                        onBlur={handleBlur}
                        onChange={handleChange}
                      />
                      <ExpiringFormFieldError name="lastName" />
                    </div>
                  </div>
                  <div className="form-group col-md-6 col-sm-12">
                    <label
                      htmlFor="firstName"
                      className="col-sm-12 control-label"
                    >
                      {t(`${BENEFICIARIES_NAMESPACE}firstName`)}
                      <sup>*</sup>
                    </label>
                    <div className="col-sm-12">
                      <input
                        className={
                          errors.firstName ? inputClasses : 'form-control'
                        }
                        name="firstName"
                        type="text"
                        value={values.firstName}
                        onBlur={handleBlur}
                        onChange={handleChange}
                      />
                      <ExpiringFormFieldError name="firstName" />
                    </div>
                  </div>
                </div>
                <div className="form-row d-flex justify-content-between">
                  <div className="form-group col-md-6 col-sm-12">
                    <label
                      htmlFor="mobileNumber"
                      className="col-sm-12 control-label"
                    >
                      {t(`${BENEFICIARIES_NAMESPACE}mobileNumber`)}
                      {(this.props.mode ===
                        HandleBeneficiaryMode.ADD_CASH_TRANSFER && (
                        <sup>*</sup>
                      )) ||
                        ((this.props.mode === HandleBeneficiaryMode.EDIT ||
                          this.props.mode === HandleBeneficiaryMode.ADD) &&
                          this.state.beneficiaryScope ===
                            BeneficiaryScope.INTERN_BENEFICIARY && (
                            <sup>**</sup>
                          ))}
                    </label>
                    <div className="col-sm-12">
                      <input
                        className={
                          errors.mobileNumber ? inputClasses : 'form-control'
                        }
                        name="mobileNumber"
                        type="text"
                        value={values.mobileNumber}
                        onChange={handleChange}
                        onBlur={handleBlur}
                      />
                      <ExpiringFormFieldError name="mobileNumber" />
                    </div>
                  </div>
                  {this.props.mode !==
                    HandleBeneficiaryMode.ADD_CASH_TRANSFER && (
                    <div className="form-group col-md-6 col-sm-12">
                      <label
                        htmlFor="accountNumber"
                        className="col-sm-12 control-label"
                      >
                        {t(`${BENEFICIARIES_NAMESPACE}accountNumber`)}
                        {((this.props.mode === HandleBeneficiaryMode.EDIT ||
                          this.props.mode === HandleBeneficiaryMode.ADD) &&
                          this.state.beneficiaryScope ===
                            BeneficiaryScope.INTERN_BENEFICIARY && (
                            <sup>**</sup>
                          )) || <sup>*</sup>}
                      </label>
                      <div className="col-sm-12">
                        <input
                          className={
                            errors.accountNumber ? inputClasses : 'form-control'
                          }
                          name="accountNumber"
                          type="text"
                          value={values.accountNumber}
                          onChange={handleChange}
                          onBlur={handleBlur}
                        />
                        <ExpiringFormFieldError name="accountNumber" />
                      </div>
                    </div>
                  )}
                </div>
                {this.props.mode !==
                  HandleBeneficiaryMode.ADD_CASH_TRANSFER && (
                  <>
                    <div className="form-row d-flex justify-content-between">
                      <div className="form-group col-md-6 col-sm-12">
                        <label htmlFor="scope" className="col-sm-12">
                          {t(`${BENEFICIARIES_NAMESPACE}otherBank`)}
                        </label>
                        <div className="col-sm-12">
                          <InputSwitch
                            name="scope"
                            checked={
                              this.state.beneficiaryScope ===
                              BeneficiaryScope.EXTERN_BENEFICIARY
                            }
                            onChange={() => {
                              this.setState({
                                beneficiaryScope:
                                  this.state.beneficiaryScope ===
                                  BeneficiaryScope.EXTERN_BENEFICIARY
                                    ? BeneficiaryScope.INTERN_BENEFICIARY
                                    : BeneficiaryScope.EXTERN_BENEFICIARY,
                              });
                            }}
                          />
                        </div>
                      </div>
                    </div>
                    {this.state.beneficiaryScope ===
                      BeneficiaryScope.EXTERN_BENEFICIARY && (
                      <div className="form-row d-flex justify-content-between">
                        <div className="form-group col-md-4 col-sm-12">
                          <label
                            htmlFor="bankCode"
                            className="col-sm-12 control-label"
                          >
                            {t(`${BENEFICIARIES_NAMESPACE}bankCode`)}
                            <sup>*</sup>
                          </label>
                          <div className="col-sm-12">
                            <input
                              className={
                                errors.bankCode ? inputClasses : 'form-control'
                              }
                              name="bankCode"
                              type="text"
                              value={values.bankCode}
                              onChange={handleChange}
                              onBlur={handleBlur}
                            />
                            <ExpiringFormFieldError name="bankCode" />
                          </div>
                        </div>
                        <div className="form-group col-md-4 col-sm-12">
                          <label
                            htmlFor="bankCode"
                            className="col-sm-12 control-label"
                          >
                            {t(`${BENEFICIARIES_NAMESPACE}atmCode`)}
                            <sup>*</sup>
                          </label>
                          <div className="col-sm-12">
                            <input
                              className={
                                errors.atmCode ? inputClasses : 'form-control'
                              }
                              name="atmCode"
                              type="text"
                              value={values.atmCode}
                              onChange={handleChange}
                              onBlur={handleBlur}
                            />
                            <ExpiringFormFieldError name="atmCode" />
                          </div>
                        </div>
                        <div className="form-group col-md-4 col-sm-12">
                          <label
                            htmlFor="bankCode"
                            className="col-sm-12 control-label"
                          >
                            {t(`${BENEFICIARIES_NAMESPACE}ribKey`)}
                            <sup>*</sup>
                          </label>
                          <div className="col-sm-12">
                            <input
                              className={
                                errors.ribKey ? inputClasses : 'form-control'
                              }
                              name="ribKey"
                              type="text"
                              value={values.ribKey}
                              onChange={handleChange}
                              onBlur={handleBlur}
                            />
                            <ExpiringFormFieldError name="ribKey" />
                          </div>
                        </div>
                      </div>
                    )}
                  </>
                )}
              </form>
              <p className="champs_obligatoires">
                <sup>(*)</sup>
                {t(`${BENEFICIARIES_NAMESPACE}mandatoryFields`)}
              </p>
              {(this.props.mode === HandleBeneficiaryMode.EDIT ||
                this.props.mode === HandleBeneficiaryMode.ADD) &&
                this.state.beneficiaryScope ===
                  BeneficiaryScope.INTERN_BENEFICIARY && (
                  <p className="champs_obligatoires double-asterix">
                    <sup>(**)</sup>
                    {t(`${BENEFICIARIES_NAMESPACE}mandatoryFieldAtLeast`)}
                  </p>
                )}
            </div>
          )}
        </Formik>
      </FormModal>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  editBeneficiary: beneficiary => {
    dispatch(editBeneficiary(beneficiary));
  },
  addBeneficiary: beneficiary => {
    dispatch(addBeneficiary(beneficiary));
  },
  addCashTransferBeneficiary: beneficiary => {
    dispatch(addCashTransferBeneficiary(beneficiary));
  },
  addTransferBeneficiary: beneficiary => {
    dispatch(addTransferBeneficiary(beneficiary));
  },
});

export default withTranslation()(
  connect(
    null,
    mapDispatchToProps,
  )(HandleBeneficiaryModal),
);
