import React from 'react';
import { connect } from 'react-redux';
import { MultiSelect } from 'primereact/multiselect';
import { t } from '../../../i18n';
import { withTranslation } from 'react-i18next';
import Panel from '../../../components/Panel/Panel';
import { FAVORITES_NAMESPACE } from '../constants';
import DataTable from '../../../components/DataTable/CustomDataTable';
import {
  getFavorites,
  deleteFavorite,
} from '../../../redux/favorites/favorites.actions';
import { removeRepeatedElements } from '../../../utils/TableUtils';
import {
  showMessage,
  hideMessage,
} from '../../../redux/messages/messages.actions';
import { activateTabWithParams } from '../../../redux/menu/menu.actions';
import { initTransfer } from '../../../redux/transfer/transfer.actions';
import { initCashTransfer } from '../../../redux/cashTransfer/cashTransfer.actions';
import FormattedNumber from '../../../components/FormattedNumber/FormattedNumber';
import FormatsConfiguration from '../../../utils/FormatsConfiguration';
import AccountColumn from '../../../components/AccountColumn/AccountColumn';

const BeneficiaryColumn = props => (
  <span className="text-strong">
    {props.beneficiaryName}
    <br />
    <span className="text-light">ID : {props.beneficiaryAccountNumber}</span>
  </span>
);

const FavoriteTypeColumn = props => (
  <span className="text-strong">
    {t(`${FAVORITES_NAMESPACE}${props.transactionType}`)}
  </span>
);
const _handleTransaction = (
  rowData,
  favoriteComponentProps,
  favorites,
  initCashTransfer,
  initTransfer,
) => {
  const activateTabRequest = {
    activeTab: rowData.transactionType,
    activetabParams: {
      fromFavorites: true,
      transactionId: rowData.transactionId,
    },
  };
  favoriteComponentProps.activateTabWithParams(activateTabRequest);
  const favorite = favorites.filter(
    favorite => favorite.transactionId === rowData.transactionId,
  )[0];
  favorite.transactionType === 'transfer'
    ? initTransfer(
        favorite.clientAccount,
        favorite.beneficiary,
        favorite.label,
        favorite.amount,
        favorite.description,
        false,
      )
    : initCashTransfer(
        favorite.clientAccount,
        favorite.beneficiary,
        favorite.label,
        favorite.amount,
        favorite.description,
        false,
      );
};
const _deleteTransaction = (rowData, favoriteComponentProps) => {
  favoriteComponentProps.deleteFavorite(
    rowData.transactionType,
    rowData.transactionId,
  );
  favoriteComponentProps.hideMessage();
};
const ActionsButtonsColumn = ({
  rowData,
  favoriteComponentProps,
  favorites,
  initCashTransfer,
  initTransfer,
}) => (
  <div className="action-btn">
    <button
      className="btn btn-clear text-alert btn-sm"
      onClick={() =>
        _handleTransaction(
          rowData,
          favoriteComponentProps,
          favorites,
          initCashTransfer,
          initTransfer,
        )
      }
    >
      <span className="design-button md-trigger popin_btn">
        {t(`${FAVORITES_NAMESPACE}${rowData.transactionType}`)}
      </span>
    </button>
    <button
      className="btn btn-secondary btn-sm"
      onClick={() =>
        favoriteComponentProps.showMessage(
          t(`${FAVORITES_NAMESPACE}deletionConfirm`),
          'WARN',
          t(`${FAVORITES_NAMESPACE}confirm`),
          true,
          () => _deleteTransaction(rowData, favoriteComponentProps),
        )
      }
    >
      <span className="actions_btn md-trigger popin_btn remove" />
    </button>
  </div>
);
let accountsNames = [];
class Favorites extends React.Component {
  state = {
    displayStepper: false,
    accountUnique: null,
    uniqueType: null,
  };
  componentDidMount() {
    this.props.getFavorites();
  }
  _buildDataTableRows = () =>
    this.props.favorites.map(transaction => {
      const {
        transactionId,
        clientAccount,
        beneficiary,
        label,
        amount,
        description,
        transactionType,
      } = transaction;
      accountsNames.push(clientAccount.accountName);
      return {
        transactionId: transactionId,
        clientName: clientAccount.accountName,
        clientAccountNumber: clientAccount.accountNumber,
        beneficiaryName: beneficiary.name,
        beneficiaryAccountNumber: beneficiary.accountNumber,
        transactionType: transactionType,
        labelRow: label,
        rowAmount: amount,
        rowDescription: description,
      };
    });

  _clientTemplate = rowData => (
    <AccountColumn
      accountName={rowData.clientName}
      accountNumber={rowData.clientAccountNumber}
    />
  );
  _beneficiaryTemplate = rowData => (
    <BeneficiaryColumn
      beneficiaryName={rowData.beneficiaryName}
      beneficiaryAccountNumber={rowData.beneficiaryAccountNumber}
    />
  );
  _balanceFormatter = data => (
    <FormattedNumber
      number={data.rowAmount}
      format={FormatsConfiguration.AmountFormat}
      currency={t('Components:currency')}
    />
  );

  _typeTemplate = rowData => (
    <FavoriteTypeColumn transactionType={rowData.transactionType} />
  );
  _actionTemplate = (rowData, favorites, initCashTransfer, initTransfer) => (
    <ActionsButtonsColumn
      initCashTransfer={initCashTransfer}
      initTransfer={initTransfer}
      favorites={favorites}
      transactionHandle={this._transactionHandle}
      rowData={rowData}
      favoriteComponentProps={this.props}
    />
  );
  _onAccountUniqueChange = event => {
    this.dt.filter(event.value, 'clientName', 'in');
    this.setState({ accountUnique: event.value });
  };
  _onTypeUniqueChange = event => {
    this.dt.filter(event.value, 'type', 'in');
    this.setState({ uniqueType: event.value });
  };
  _accountFilterComponent = (element, options) => (
    <MultiSelect
      style={{ width: '100%' }}
      value={element}
      options={options}
      onChange={this._onAccountUniqueChange}
    />
  );
  _typeFilterComponent = (element, options) => (
    <MultiSelect
      style={{ width: '100%' }}
      value={element}
      options={options}
      onChange={this._onTypeUniqueChange}
    />
  );
  render() {
    let rows = this._buildDataTableRows();
    let uniqueAccountsNames = removeRepeatedElements(accountsNames).map(
      accountName => ({
        label: accountName,
        value: accountName,
      }),
    );
    let uniqueType = [
      {
        label: t(`${FAVORITES_NAMESPACE}transfer`),
        value: t(`${FAVORITES_NAMESPACE}transfer`),
      },
      {
        label: t(`${FAVORITES_NAMESPACE}cashTransfer`),
        value: t(`${FAVORITES_NAMESPACE}cashTransfer`),
      },
    ];
    return (
      <div className="row mx-auto">
        <div className="col-sm-12">
          <Panel title={t(`${FAVORITES_NAMESPACE}favorites`)}>
            <DataTable
              value={rows}
              onRowReorder={e => (rows = e.value)}
              reference={el => (this.dt = el)}
              columnResizeMode="fit"
              cols={[
                {
                  field: 'clientName',
                  header: t(`${FAVORITES_NAMESPACE}senderAccount`),
                  body: this._clientTemplate,
                  filterElement: this._accountFilterComponent(
                    this.state.accountUnique,
                    uniqueAccountsNames,
                  ),
                  filter: true,
                },
                {
                  field: 'beneficiaryName',
                  header: t(`${FAVORITES_NAMESPACE}beneficiary`),
                  body: this._beneficiaryTemplate,
                },
                {
                  field: 'type',
                  header: t(`${FAVORITES_NAMESPACE}transferType`),
                  body: this._typeTemplate,
                  filterElement: this._typeFilterComponent(
                    this.state.uniqueType,
                    uniqueType,
                  ),
                  filter: true,
                },
                {
                  field: 'labelRow',
                  header: t(`${FAVORITES_NAMESPACE}favoriteLabel`),
                },
                {
                  field: 'rowAmount',
                  header: t(`${FAVORITES_NAMESPACE}amount`),
                  body: this._balanceFormatter,
                },
                {
                  field: 'rowDescription',
                  header: t(`${FAVORITES_NAMESPACE}description`),
                },
                {
                  field: 'action',
                  header: t(`${FAVORITES_NAMESPACE}actions`),
                  body: rowData =>
                    this._actionTemplate(
                      rowData,
                      this.props.favorites,
                      this.props.initCashTransfer,
                      this.props.initTransfer,
                    ),
                },
              ]}
            />
          </Panel>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => ({
  showMessage: (
    text,
    severity,
    validateTitle,
    hasCancelBtn,
    handleValidate,
  ) => {
    dispatch(
      showMessage(text, severity, validateTitle, hasCancelBtn, handleValidate),
    );
  },
  hideMessage: () => {
    dispatch(hideMessage());
  },
  getFavorites: () => {
    dispatch(getFavorites());
  },
  deleteFavorite: (transactionType, transactionId) => {
    dispatch(deleteFavorite(transactionType, transactionId));
  },
  activateTabWithParams: activateTabRequest => {
    dispatch(activateTabWithParams(activateTabRequest));
  },
  initCashTransfer: (
    clientAccount,
    beneficiary,
    label,
    amount,
    description,
    favoris,
  ) => {
    dispatch(
      initCashTransfer({
        clientAccount,
        beneficiary,
        label,
        amount,
        description,
        favoris,
      }),
    );
  },
  initTransfer: (
    clientAccount,
    beneficiary,
    label,
    amount,
    description,
    favoris,
  ) => {
    dispatch(
      initTransfer({
        clientAccount,
        beneficiary,
        label,
        amount,
        description,
        favoris,
      }),
    );
  },
});
const mapStateToProps = state => ({
  activateTabRequest: state.activeTabParams,
  favorites: state.Favorites.favorites,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(Favorites),
);
