import React from 'react';
import { pdfStyles } from './PdfTemplate.styles';
import { t } from '../../i18n';
import { withTranslation } from 'react-i18next';
import * as DateUtils from '../../utils/DateUtils';
import Logo from '../../components/Logo/PNGLogo';
import FormatsConfiguration from '../../utils/FormatsConfiguration';
import FormattedNumber from '../../components/FormattedNumber/FormattedNumber';
import { HISTORY_NAMESPACE } from '../../features/Transfers/constants';
import AccountColumn from '../../components/AccountColumn/AccountColumn';

const TransfersHistoryTemplate = props => {
  const { title, idLogo, idHeader, idTable } = props;
  const operations = props.tableRows.map(row => (
    <tr key={row.code}>
      <td>
        {DateUtils.formatDateTime(
          row.date,
          FormatsConfiguration.DateTimeFormat.fullDate,
        )}
      </td>
      <td>
        <AccountColumn
          accountName={row.clientName}
          accountNumber={row.clientNum}
        />
      </td>
      <td>{row.beneficiaryName}</td>
      <td>{row.code}</td>
      <td>
        <FormattedNumber
          number={row.amount}
          format={FormatsConfiguration.AmountFormat}
        />
      </td>
    </tr>
  ));

  return (
    <div id="pdf" style={pdfStyles.pdf}>
      <table id={idLogo}>
        <tbody>
          <tr>
            <td>
              <Logo />
            </td>
          </tr>
        </tbody>
      </table>
      <table id={idHeader}>
        <tbody>
          <tr>
            <td />
            <td>
              <b>{title}</b>
            </td>
            <td />
          </tr>
        </tbody>
      </table>
      <table id={idTable}>
        <thead>
          <tr>
            <th>{t(`${HISTORY_NAMESPACE}dateClnLabel`)}</th>
            <th>{t(`${HISTORY_NAMESPACE}accountClnLabel`)}</th>
            <th>{t(`${HISTORY_NAMESPACE}beneficiaryClnLabel`)}</th>
            <th>{t(`${HISTORY_NAMESPACE}referenceClnLabel`)}</th>
            <th>{t(`${HISTORY_NAMESPACE}amountClnLabel`)}</th>
          </tr>
        </thead>
        <tbody>{operations}</tbody>
      </table>
    </div>
  );
};

export default withTranslation()(TransfersHistoryTemplate);
